#tile1{
    background-color: rgb(214, 209, 209);
    height:800px;
    width:15% ;
    border-radius: 8px;
    padding: 10px;
    padding-right: 20px;
    float: left; 
    margin-right: 30px; 
    margin-left: 30px;
    margin-top: 20px; 
}
#heading{
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 50px;
}
td{
    background-color: #d0f0c0;
    width: 80px;
    height: 30px;
    text-align: center;
    border-radius: 5px;
    padding: 10px ;
    padding-left: 10px;
}
td:hover{
    background-color: #138808;
}
.butt:hover{
    background-color: red;
}
#tile2{
    background-color: white;
    height:500px;
    width:16%;
    border-radius: 8px;
    float:left;
    
    margin: 20px;
}
#tilecomponent2{
    padding: 20px;
    height: 160px;
}
#head{
    color: #062a78;
    font-size: 22px;
    font-weight: bold;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
#info{
    color: black;
    font-weight: bold;
   font-size: large;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    line-height: 2;
}
.image{
    width:200px;
    height:200px;
    margin-left:30px;
    margin-top:20px;
    background-color:#c4c2c2;
}


/*media*/
@media only screen and (max-width:700px){
    .row{
        text-align:center;
    }
    .col-2, .col-3, .col-4{
            flex-basis:100%;
    }
    .single-product .row{
      text-align:left;
    }
    .single-product .col-2{
        padding:20px 0;}
    
    .single-product h1{
        font-size:26px;
        line-height:32px;
    }
    .cart-info p{
        display:none;
    }
}
